import React from "react";

interface PostCardProps {
  postItem: {
    id: number | string;
    title: string;
    category: string;
    publishedAt: string | Date;
    content: string;
    image?: string;
  };
}

const PostCard: React.FC<PostCardProps> = ({ postItem }) => {
  return (
    <article className="max-w-md mx-auto bg-white rounded-lg shadow-md overflow-hidden hover:shadow-xl transition-shadow duration-300 flex flex-col md:flex-row">
      {/* Hình ảnh bên trái, chiếm 1/3 chiều ngang trên màn hình rộng */}
      {postItem.image && (
        <img
          src={postItem.image}
          alt={postItem.title}
          className="w-full md:w-1/3 h-48 md:h-auto object-cover"
          loading="lazy"
        />
      )}

      {/* Nội dung bên phải */}
      <div className="p-6 flex flex-col justify-between md:w-2/3">
        <div>
          <h2 className="text-2xl font-bold text-gray-900 hover:text-blue-600 transition-colors cursor-pointer mb-2">
            <a href={`/posts/${postItem.id}`}>{postItem.title}</a>
          </h2>

          <div className="flex items-center text-sm text-gray-500 space-x-3 mb-4">
            <span className="italic capitalize">{postItem.category}</span>
            <span>•</span>
            <time dateTime={new Date(postItem.publishedAt).toISOString()}>
              {new Date(postItem.publishedAt).toLocaleDateString()}
            </time>
          </div>

          <p
            className="text-gray-700 leading-relaxed"
            dangerouslySetInnerHTML={{ __html: postItem.content.slice(0, 150) + "..." }}
          />
        </div>

        <a
          href={`/posts/${postItem.id}`}
          className="mt-6 inline-block bg-red-600 text-white font-semibold px-5 py-2 rounded-lg hover:bg-red-700 transition-colors"
          aria-label={`Đọc thêm bài viết: ${postItem.title}`}
        >
          Đọc thêm →
        </a>
      </div>
    </article>
  );
};

export default PostCard;
